<template>
    <div class="container">
        <TopButton title="상세페이지" />
        <div class="itemBox">
            <div class="itemimg">
                <img :src="product.image" alt="상품이미지" />
            </div>
            <div class="itemText">
                <p class="item_tag">#000 #000 #000</p>
                <p class="item_score"><img src="@/assets/img/leaf.svg" alt="score" /> 4.3점</p>
                <p class="item_title">{{ product.title }}</p>
                <p class="item_price">{{ product.price }} 원</p>
            </div>
        </div>
        <div class="buttons">
            <button @click="addToCart">장바구니</button>
            <button @click="link2">바로구매</button>
        </div>
    </div>
</template>
<script>
import TopButton from '../components/TopButton.vue';
import CategoryItem from '@/assets/js/CategoryItem.js';

export default {
    name: 'ProductPage',
    components: {
        TopButton
    },
    data() {
        return {
            product: {} // 선택된 상품 데이터를 저장
        };
    },
    created() {
        const productId = this.$route.params.id;
        this.product = CategoryItem.find((item) => item.id === parseInt(productId));
        if (!this.product) {
            console.error('상품을 찾을 수 없습니다.');
        }
    },
    methods: {
        // 장바구니로 상품 전달
        addToCart() {
            console.log('전달된 상품 데이터:', JSON.stringify(this.product));
            this.$router.push({
                name: 'ShoppingCart',
                query: { product: JSON.stringify(this.product) }
            });
        },
        link2() {
            alert('바로구매 기능은 아직 구현되지 않았습니다.');
        }
    }
};
</script>
<style scoped>
.itemBox {
    width: 100%;
    margin-top: 20px;
}
.itemBox .itemimg {
    width: 100%;
    height: 350px;
}
.itemBox .itemimg img {
    width: 100%;
    height: 350px;
}
.itemBox .itemText {
    width: 90%;
    margin: 0 auto;
}
.itemBox .itemText p {
    text-align: left;
    margin: 0;
    font-size: 1em;
    line-height: 27px;
}

.itemBox .itemText img {
    margin-right: 5px;
    filter: invert(10%) sepia(89%) saturate(6058%) hue-rotate(83deg) brightness(108%) contrast(86%);
}

.itemBox .itemText .item_score {
    display: flex;
    align-items: center;
    margin-bottom: 5px;
}

.itemBox .itemText .item_title {
    font-size: 1.2em;
    letter-spacing: -0.5px;
    font-weight: 700;
}
.itemBox .itemText .item_price {
    margin-top: 22px;
    font-size: 1.1em;
}

.buttons {
    text-align: center;
    margin-top: 30px;
}

.buttons button {
    border: none;
    border-radius: 20px;
    background-color: #acacac;
    padding: 7px 15px;
    margin: 5px;
    color: white;
    font-weight: bold;
    cursor: pointer;
}
</style>
